import React, { Component } from 'react';
import { Card } from '@uifabric/react-cards';
import { Stack, Text, Icon, FontWeights, ActionButton, Image } from 'office-ui-fabric-react';

class MCard extends Component {
    siteTextStyles = {
        root: {
            color: '#025F52',
            fontWeight: FontWeights.semibold
        }
    };
    descriptionTextStyles = {
        root: {
            color: '#333333',
            fontWeight: FontWeights.semibold
        }
    };
    helpfulTextStyles = {
        root: {
            color: '#333333',
            fontWeight: FontWeights.regular
        }
    };
    footerCardSectionStyles = {
        root: {
            borderTop: '1px solid #F3F2F1'
        }
    };
    backgroundImageCardSectionStyles = {
        root: {
            // backgroundImage: 'url(https://placehold.it/256x144)',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
            height: 144
        }
    };
    dateTextStyles = {
        root: {
            color: '#505050',
            fontWeight: 600
        }
    };
    subduedTextStyles = {
        root: {
            color: '#666666'
        }
    };
    render() {
        return (<Card aria-label="Clickable vertical card with image bleeding at the top of the card" tokens={{ childrenMargin: 12 }} height="500" style={{ height: 450 }}>
            <Card.Section fill verticalAlign="end" styles={this.backgroundImageCardSectionStyles} tokens={this.backgroundImageCardSectionTokens}>
                <Image src={this.props.cardinfo.KeyImage} />
            </Card.Section>
            <Card.Section>
                <Text variant="small" styles={this.subduedTextStyles}>
                    {this.props.cardinfo.Category.CategoryName || this.props.cardinfo.Category.ParentCategoryName}
                </Text>
                <Text styles={this.descriptionTextStyles} onClick={this.props.onCardClick}>{this.props.cardinfo.Name}</Text>
            </Card.Section>
            <Card.Section tokens={this.agendaCardSectionTokens}>
                <Text variant="small" styles={this.descriptionTextStyles}>
                    {this.props.cardinfo.Description}
                </Text>

            </Card.Section>
            <Card.Item grow={1}>
                <span />
            </Card.Item>
            <Card.Section horizontal tokens={this.attendantsCardSectionTokens}>
                <ActionButton text={'Rating: ' + this.props.cardinfo.Rating} styles={{
                    root: {
                        border: 'none',
                        color: '#333333',
                        height: 'auto',
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                            ':hover': {
                                color: '#0078D4'
                            }
                        }
                    },
                    textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                    }
                }} />
                <ActionButton text={'Subscribers: ' + this.props.cardinfo.Subscribers} styles={{
                    root: {
                        border: 'none',
                        color: '#333333',
                        height: 'auto',
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                            ':hover': {
                                color: '#0078D4'
                            }
                        }
                    },
                    textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                    }
                }} />
                <ActionButton text={'Price: ' + this.props.cardinfo.Price} styles={{
                    root: {
                        border: 'none',
                        color: '#333333',
                        height: 'auto',
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                            ':hover': {
                                color: '#0078D4'
                            }
                        }
                    },
                    textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                    }
                }} />
            </Card.Section>
            <Card.Section horizontal styles={this.footerCardSectionStyles} tokens={this.footerCardSectionTokens}>
                <Icon iconName="RedEye" styles={{
                    root: {
                        color: '#0078D4',
                        fontSize: 16,
                        fontWeight: FontWeights.regular
                    }
                }} title={this.props.cardinfo.ViewsCount} />
                <Icon iconName="SingleBookmark" styles={{
                    root: {
                        color: '#0078D4',
                        fontSize: 16,
                        fontWeight: FontWeights.regular
                    }
                }} />
                <Stack.Item grow={1}>
                    <span />
                </Stack.Item>
                <Icon iconName="MoreVertical" styles={{
                    root: {
                        color: '#0078D4',
                        fontSize: 16,
                        fontWeight: FontWeights.regular
                    }
                }} />
            </Card.Section>
        </Card>);
    }
}

export default MCard;