import * as actionNames from './index';
import httpService from '../services/httpService';
const handleData = (url) => {
    return (dispatch) => {
        dispatch(getData([], true))
        httpService.get(url).then((res) => {
            dispatch(getData(res.data, false))
        })
    }
}
const getData = (data, isLoading) => {
    return {
        type: actionNames.GET_DATA,
        payload: { isLoading: isLoading, data: data }
    }
}

export {
    handleData
}