import React, { Component } from 'react';
import { Text, PrimaryButton, DefaultButton, Stack, ContextualMenuItemType, getTheme, mergeStyleSets, FontWeights, FontSizes, IconButton, Modal } from 'office-ui-fabric-react'
import { DDL, Input, Check } from '../fabric.ui';
import { Countries } from '../../shared/countries';
import profileImage from '../../images/profile.jpg'
import userManager from '../../utils/userManager';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import httpService from '../../services/httpService'


// Themed styles for the example.
const theme = getTheme();
const contentStyles = mergeStyleSets({
    container: {
        display: 'flex',
        flexFlow: 'column nowrap',
        alignItems: 'stretch'
    },
    header: [
        // tslint:disable-next-line:deprecation
        theme.fonts.xLargePlus,
        {
            flex: '1 1 auto',
            borderTop: `4px solid ${theme.palette.themePrimary}`,
            color: theme.palette.neutralPrimary,
            display: 'flex',
            fontSize: FontSizes.xLarge,
            alignItems: 'center',
            fontWeight: FontWeights.semibold,
            padding: '12px 12px 14px 24px'
        }
    ],
    body: {
        flex: '4 4 auto',
        padding: '0 24px 24px 24px',
        overflowY: 'hidden',
        selectors: {
            p: {
                margin: '14px 0'
            },
            'p:first-child': {
                marginTop: 0
            },
            'p:last-child': {
                marginBottom: 0
            }
        }
    }
});

const iconButtonStyles = mergeStyleSets({
    root: {
        color: theme.palette.neutralPrimary,
        marginLeft: 'auto',
        marginTop: '4px',
        marginRight: '2px'
    },
    rootHovered: {
        color: theme.palette.neutralDark
    }
});
class Header extends Component {
    authService;
    state = {
        showModal: false,
        registration: { Email: null, Name: null, Env: 'TST', Roles: [] }
    }

    menuItems = [
        {
            key: 'myDashboard',
            text: 'My Dashboard',
            onClick: () => this.props.dispatch(push("/dashboard"))

        },
        {
            key: 'divider_1',
            itemType: ContextualMenuItemType.Divider
        },
        {
            key: 'logout',
            text: 'Logout',
            onClick: () => this.handleLogout()
        }

    ];
    menuProps = {
        shouldFocusOnMount: true,
        items: this.menuItems
    }
    categoriesMenu = {
        shouldFocusOnMount: true,
        items: [
            {
                key: 'apps',
                text: 'Apps',
                onClick: () => this.props.dispatch(push("/producttypes?type=Apps"))
            },
            {
                key: 'services',
                text: 'Services',
                onClick: () => this.props.dispatch(push("/producttypes?type=Services"))
            }

        ]
    }
    handleLogin = () => {
        localStorage.setItem("__url", `${window.location.pathname}`)
        userManager.signinRedirect();
    }
    handleLogout = () => {
        userManager.removeUser();
        userManager.signoutRedirect();
    }
    handleSignup = () => {
        this.setState({ showModal: true })
    }
    handleLoginButtons = () => {
        if (this.props.oidc.user) {
            return (<div className="user-profile">
                <Stack horizontal tokens={{ childrenGap: 3 }}>  <img src={profileImage} alt="P" />
                    <DefaultButton text={this.props.oidc.user.profile.email} menuProps={this.menuProps} /></Stack>
            </div>)

        } else {
            return <Stack horizontal tokens={{ childrenGap: 10 }} className="right">
                <DefaultButton onClick={this.handleLogin} text="Login" />
                <PrimaryButton text="Sign Up" onClick={this.handleSignup} />
            </Stack>;
        }
    }
    onInputChange = (e, val) => {
        let { registration } = this.state;
        registration[e.target.name] = val;
        this.setState({ registration })
    }
    onCheckChange = (role, val) => {
        debugger
        let { registration } = this.state;
        const idx = registration.Roles.indexOf(role);
        if (idx === -1) { registration.Roles.push(role) }
        else { registration.Roles.push(idx, 1) }
        this.setState({ registration })
    }
    handleRegistration = () => {
        httpService.post(process.env.REACT_APP_API_END_POINT + 'MarketPlaceRegistration', this.state.registration).then((response) => { })
    }
    render() {
        return (
            <React.Fragment>
                <div className="header header-top-add header-fixed-top">
                    <div className="container">
                        <div className="ms-Grid" dir="ltr">
                            <div className="ms-Grid-row">
                                <div className="ms-Grid-col ms-sm6 ms-md3 ms-lg6 ms-xxl2"> <Text variant={'large'} block>Marketplace</Text> <DefaultButton text={this.props.oidc?.user?.profile.email} menuProps={this.categoriesMenu} /></div>
                                <div className="ms-Grid-col ms-sm10 ms-md6 ms-lg6 ms-xxl10">
                                    <Stack horizontal horizontalAlign={'end'} tokens={{ childrenGap: 100 }}>
                                        <div className="ms-Grid-col ms-sm6 ms-md-3 ms-lg3">
                                            <DDL options={Countries} value="SG" />
                                        </div>
                                        <div className="ms-Grid-col ms-sm6 ms-lg2 ms-md-9">
                                            {this.handleLoginButtons()}
                                        </div>
                                    </Stack>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container">

                </div>
                <Modal
                    titleAriaId={'signuppopup'}
                    subtitleAriaId={'signuppopupsub'}
                    isOpen={this.state.showModal}
                    onDismiss={() => this.setState({ showModal: false })}
                    isBlocking={true}
                    containerClassName={contentStyles.container}>
                    <div className={contentStyles.header}>
                        <span id={'signuppopup'}>Sign up</span>
                        <IconButton
                            styles={iconButtonStyles}
                            iconProps={{ iconName: 'Cancel' }}
                            ariaLabel="Close popup modal"
                            onClick={() => this.setState({ showModal: false })}
                        />
                    </div>
                    <div id={'signuppopup'} className={contentStyles.body}>
                        <Stack horizontal tokens={{ childrenGap: 20 }}>
                            <div className="ms-Grid" dir="ltr">
                                <div className="ms-Grid-col col-md12">
                                    <div style={{ width: 100 }}></div>
                                </div>
                            </div>
                            <div className="ms-Grid" dir="ltr">
                                <div className="ms-Grid-row">
                                    <div className="ms-Grid-col col-md12">
                                        <Stack horizontal horizontalAlign="start" tokens={{ childrenGap: 40 }}>
                                            <Check label="Buyer" checked={this.state.registration.Roles.indexOf('Buyer') > -1} name="Buyer" onChange={(e, val) => this.onCheckChange('Buyer', val)} />
                                            <Check label="Seller" checked={this.state.registration.Roles.indexOf('Seller') > -1} name="Seller" onChange={(e, val) => this.onCheckChange('Seller', val)} />

                                        </Stack>
                                        <Stack style={{ width: 450 }}>
                                            <Input style={{ width: 150 }} label="Name" name="Name" placeholder="Name" onChange={(e, val) => this.onInputChange(e, val)} />
                                            <Input style={{ width: 150 }} label="Email" name="Email" placeholder="Email" onChange={(e, val) => this.onInputChange(e, val)} />
                                        </Stack>
                                        <Stack horizontal horizontalAlign="end" tokens={{ childrenGap: 10 }} style={{ marginTop: 20 }}>
                                            <PrimaryButton style={{ width: 100 }} text="Register" onClick={() => this.handleRegistration()} />
                                            <DefaultButton style={{ width: 100 }} onClick={() => this.setState({ showModal: false })} text="Cancel" />
                                        </Stack>
                                    </div>
                                </div>
                            </div>
                        </Stack>

                    </div>
                </Modal>
            </React.Fragment>
        );
    }
}
const mapStateToProps = (state) => {
    const { oidc } = state;
    return {
        oidc
    }

}
const mapDispatchToProps = (dispatch) => {
    return {
        dispatch
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Header);
