import React, { Component } from 'react';
import { GridRow } from '../common';
import { handleData } from '../../actions/action-creators';
import { connect } from 'react-redux';
import Products from '../products';
import { push } from 'react-router-redux';
import { Mshimmer } from '../fabric.ui'
class Dashboard extends Component {
    state = {}
    history;
    componentDidMount() {
        this.props.fetchData();
    }
    onProductClick(product) {

        this.props.dispatch(push(`/productDetail/${product.Id}`));
    }
    render() {
        return (
            <React.Fragment>
                <h4>Trending</h4>
                <GridRow>
                    <Mshimmer loadShimmer={this.props.data.isLoading} />
                    <Products onProductClick={(product) => this.onProductClick(product)} data={this.props.data?.data?.Trending} />
                </GridRow>
                <h4>Newly Added</h4>
                <GridRow>
                    <Mshimmer loadShimmer={this.props.data.isLoading} />
                    <Products data={this.props.data?.data?.NewlyAdded} />
                </GridRow>
            </React.Fragment >)
    }

}
const mapStateToProps = (state) => {
    const { data, oidc } = state;
    return {
        data,
        oidc
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        fetchData: () => dispatch(handleData(process.env.REACT_APP_API_END_POINT + 'products/getDashboardProducts?userName=')),
        dispatch
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);