import { createStore, applyMiddleware, compose } from "redux";
import { browserHistory } from "react-router";
import {
  routerMiddleware
} from "react-router-redux";
import { loadUser } from "redux-oidc";
import reducer from "./reducers";
import userManager from "./utils/userManager";
import thunk from "redux-thunk";
const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
// create the middleware with the userManager
// const oidcMiddleware = createOidcMiddleware(userManager);

const loggerMiddleware = store => next => action => {
  // console.log("Action type:", action.type);
  // console.log("Action payload:", action.payload);
  // console.log("State before:", store.getState());
  next(action);
  // console.log("State after:", store.getState());
};

const initialState = {};

const createStoreWithMiddleware = compose(
  applyMiddleware(loggerMiddleware, routerMiddleware(browserHistory))
)(createStore);

const store = createStoreWithMiddleware(reducer, initialState, composeEnhancer(applyMiddleware(thunk)));
loadUser(store, userManager);

export default store;
