import React, { Component } from "react";
import httpService from "./../../services/httpService";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

class ProductDetail extends Component {
  state = {
    proddetail: null
  };
  render() {
    return (
      <React.Fragment>
        {/* <div>In product Detail</div> */}
        <div>
          {/* //Top Panel */}
          <div>Subscribers : {this.state.proddetail?.Subscribers}</div>
          <div>Rating : {this.state.proddetail?.Rating}/5</div>
          {/* //Side Panel */}
          <div>
            Tags:
            {this.state.proddetail?.Tags.map(tag => (
            <span>#{tag},</span>
          ))}
          </div>
          <div>Country/Region {this.state.proddetail?.CountryRegion}</div>
          <div>Support: {this.state.proddetail?.Support?.Title}</div>
          <div>
            Legal:
            {this.state.proddetail?.Legal?.map(leg => (
            <p>{leg.Title}</p>
          ))}
          </div>
          <div>
            Downloads:{" "}
            {this.state.proddetail?.Downloads?.map(dwn => (
              <p>{dwn.Title}</p>
            ))}
          </div>

          {/* //Main panel */}
          <div>Name : {this.state.proddetail?.Name}</div>
          <div>Description : {this.state.proddetail?.Description}</div>
          <div>{this.state.proddetail?.Includes?.Heading}</div>
          <div>
            {this.state.proddetail?.Includes?.Features.map(feature => (
              <p>{feature}</p>
            ))}
          </div>
          <div>{this.state.proddetail?.ProductInfo}</div>
          <Carousel>
            {this.state.proddetail?.Includes?.ImagesList?.map(image => (
              <div>
                <img src={image} />
              </div>
            ))}
          </Carousel>
          <div>Packages</div>
          <div>
            {this.state.proddetail?.Packages.map(newpack => {
              if (newpack.Price > 0) {
                return (
                  <div>
                    <p>{newpack.Name}</p>
                    <p>${newpack.Price}</p>
                    <p>{newpack.Description}</p>
                    <p>{newpack.DeliveryTime} days delivery</p>
                    <p>
                      {newpack.HighLights.map(highlight => (
                        <p>{highlight}</p>
                      ))}
                    </p>
                  </div>
                );
              }
            })}
          </div>
        </div>
      </React.Fragment>
    );
  }

  componentDidMount() {
    debugger
    this.fetchProductDetail(this.props.params.id);
  }

  fetchProductDetail = id => {
    httpService
      .get(
        process.env.REACT_APP_API_END_POINT +
        `products/getProductDetail?productId=${id}`
      )
      .then(resp => {
        const productdetail = resp.data;
        console.log(productdetail);
        this.setState({
          proddetail: productdetail
        });
      });
  };
}

export default ProductDetail;
