import React, { Component } from 'react';
import { MCard } from '../components/fabric.ui'
import { Stack } from 'office-ui-fabric-react';
import Mshimmer from './fabric.ui/shimmer';
class Products extends Component {
    render() {
        return (
            <Stack horizontal>
                {this.props.data?.map(product =>
                    <div className="ms-Grid-col ms-md3 ms-sm12 ms-lg-4" key={product.Id}>
                        <MCard cardinfo={product} onCardClick={() => this.props.onProductClick(product)} />
                    </div>
                )}
            </Stack>);
    }
}

export default Products;