import { GET_DATA } from '../actions'
const initialState = {
    data: [],
    isLoading: false
}
const handleDataReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_DATA:
            state = { data: action.payload.data, isLoading: action.payload.isLoading }
            return state
        default:
            return state

    }

}
export default handleDataReducer;