import React, { Component } from "react";
import queryString from "query-string";
import httpService from "./../../services/httpService";
import { Card } from "@uifabric/react-cards";
import { Redirect } from "react-router-dom";
import {
  Stack,
  Text,
  Icon,
  FontWeights,
  ActionButton,
  Image
} from "office-ui-fabric-react";

class ProductTypes extends Component {
  siteTextStyles = {
    root: {
      color: "#025F52",
      fontWeight: FontWeights.semibold
    }
  };
  descriptionTextStyles = {
    root: {
      color: "#333333",
      fontWeight: FontWeights.semibold
    }
  };
  helpfulTextStyles = {
    root: {
      color: "#333333",
      fontWeight: FontWeights.regular
    }
  };
  footerCardSectionStyles = {
    root: {
      borderTop: "1px solid #F3F2F1"
    }
  };
  backgroundImageCardSectionStyles = {
    root: {
      // backgroundImage: 'url(https://placehold.it/256x144)',
      backgroundPosition: "center center",
      backgroundSize: "cover",
      height: 144
    }
  };
  dateTextStyles = {
    root: {
      color: "#505050",
      fontWeight: 600
    }
  };
  subduedTextStyles = {
    root: {
      color: "#666666"
    }
  };
  state = { prods: [] };
  render() {
    return (
      <React.Fragment>
        {/* <a href="/producttypes?type=Apps">Apps</a>
        <a href="/producttypes?type=Services">Services</a>
        <a href="/producttypes?type=Trainings">Trainings</a> */}

        <p>Welcome to {queryString.parse(this.props.location.search).type}</p>
        <>
          {this.state.prods?.map(product => (
            <div key={product.Id} style={{ marginRight: 5, marginBottom: 10 }}>
              <Card
                aria-label="Clickable vertical card with image bleeding at the top of the card"
                tokens={{ childrenMargin: 12 }}
                height="500"
                onClick={() => {
                  this.routeToProductDetail(product.Id);
                }}
              >
                <Card.Section
                  fill
                  verticalAlign="end"
                  styles={this.backgroundImageCardSectionStyles}
                  tokens={this.backgroundImageCardSectionTokens}
                >
                  <Image src={product.KeyImage} />
                </Card.Section>
                <Card.Section>
                  <Text variant="small" styles={this.subduedTextStyles}>
                    {product.Category.CategoryName ||
                      product.Category.ParentCategoryName}
                  </Text>
                  <Text styles={this.descriptionTextStyles}>
                    {product.Name}
                  </Text>
                </Card.Section>
                <Card.Section tokens={this.agendaCardSectionTokens}>
                  <Text variant="small" styles={this.descriptionTextStyles}>
                    {product.Description}
                  </Text>
                </Card.Section>
                <Card.Item grow={1}>
                  <span />
                </Card.Item>
                <Card.Section
                  horizontal
                  tokens={this.attendantsCardSectionTokens}
                >
                  <ActionButton
                    text={"Rating: " + product.Rating}
                    styles={{
                      root: {
                        border: "none",
                        color: "#333333",
                        height: "auto",
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                          ":hover": {
                            color: "#0078D4"
                          }
                        }
                      },
                      textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                      }
                    }}
                  />
                  <ActionButton
                    text={"Subscribers: " + product.Subscribers}
                    styles={{
                      root: {
                        border: "none",
                        color: "#333333",
                        height: "auto",
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                          ":hover": {
                            color: "#0078D4"
                          }
                        }
                      },
                      textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                      }
                    }}
                  />
                  <ActionButton
                    text={"Price: " + product.Price}
                    styles={{
                      root: {
                        border: "none",
                        color: "#333333",
                        height: "auto",
                        minHeight: 0,
                        minWidth: 0,
                        padding: 0,

                        selectors: {
                          ":hover": {
                            color: "#0078D4"
                          }
                        }
                      },
                      textContainer: {
                        fontSize: 12,
                        fontWeight: FontWeights.semibold
                      }
                    }}
                  />
                </Card.Section>
              </Card>
            </div>
          ))}
        </>
      </React.Fragment>
    );
  }

  componentDidMount() {
    const queryParam = queryString.parse(this.props.location.search);
    this.fetchProductType(queryParam.type);
  }

  fetchProductType = type => {
    httpService
      .get(
        process.env.REACT_APP_API_END_POINT +
          `products/searchByCategory?categoryId=&keyword=&kind=${type}&userName=`
      )
      .then(resp => {
        const producttypedata = resp.data;
        this.setState({
          prods: producttypedata.Products
        });
      });
  };
  routeToProductDetail = id => {
    this.props.history.push(`/productDetail/${id}`);
  };
}

export default ProductTypes;
